/* Helpers */

h1,
h2,
h3,
h4,
h5,
h6,
p,
span {
    font-family: 'Comfortaa', cursive;
}

.w-text-b-shadow {
    text-transform: uppercase;
    color: #fff;
    text-shadow: 5px 5px 20px #000;
}

.fixed-nav {
    position: fixed;
}

.bg-black {
    background-color: #000;
}

.bg-light-gray {
    background-color: #D3D3D3;
}


