body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

html,
body {
    height: 100%;
    margin: 0;
}

.wrapper {
    min-height: 100%;
    /* Equal to height of footer */
    /* But also accounting for potential margin-bottom of last child */
    margin-bottom: -54px;
}

.site-footer,
.push {
    height: 54px;
}
.site-footer {
    padding: 15px;
    mask-image: gradient(linear, left 25%, left top, from(rgba(0, 0, 0, 1)), to(rgba(0, 0, 0, 0)));
    -webkit-mask-image: -webkit-gradient(linear, left 25%, left top, from(rgba(0, 0, 0, 1)), to(rgba(0, 0, 0, 0)));
}

a,
a:visited,
a:focus,
a:active,
a:hover {
    color: #000;
    text-decoration: none !important;
}
/* Default Styles */

nav.navbar.navbar-default {
    margin-bottom: 0;
}

.navbar-nav .nav-link {
    text-transform: uppercase;
}


/* Home Navigation Styles */

nav.nav-home {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    background-color: transparent !important;
}

nav.nav-home .navbar-brand {
    color: #fff;
    text-shadow: 5px 5px 20px #000;
}

nav.nav-home .navbar-nav .nav-link {
    color: #663333;
    text-shadow: 5px 5px 20px #000;
}

nav.nav-home .navbar-nav .nav-link.active {
    color: #fff !important;
}

nav.nav-home img {
    height: 40px;
    width: auto;
}

.text-brown {
    color: #663333;
}
.jumbotron {
    min-height: 100vh;
    background-size: cover;
    background-position: center;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-transform: uppercase;
    color: #fff;
    text-shadow: 5px 5px 20px #000;
    mask-image: gradient(linear, left 90%, left bottom, from(rgba(0, 0, 0, 1)), to(rgba(0, 0, 0, 0)));
    -webkit-mask-image: -webkit-gradient(linear, left 90%, left bottom, from(rgba(0, 0, 0, 1)), to(rgba(0, 0, 0, 0)));
}
/* Game Navigation Styles */

nav.nav-game {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    box-shadow: 10px 0px 5px #222;
}

nav.nav-game img {
    height: 40px;
    width: auto;
}
.card-wrapper {
    padding: 2px;
    cursor: pointer;
}

.card,
.card-body {
    padding: 1px;
}
.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


/* Custom */


/* Helpers */

h1,
h2,
h3,
h4,
h5,
h6,
p,
span {
    font-family: 'Comfortaa', cursive;
}

.w-text-b-shadow {
    text-transform: uppercase;
    color: #fff;
    text-shadow: 5px 5px 20px #000;
}

.fixed-nav {
    position: fixed;
}

.bg-black {
    background-color: #000;
}

.bg-light-gray {
    background-color: #D3D3D3;
}


